* {
  background: #ffffff;
  color: #000000;
  margin: 0;
  list-style: none;
  outline: 0;
  padding: 0;
  text-decoration: none; }

.cf {
  *zoom: 1; }

.cf:before {
  content: " ";
  display: table; }

.cf:after {
  clear: both;
  content: " ";
  display: table; }

.category-news {
  width: 300px; }

.category-news .section-title {
  background: #fff;
  border-bottom: 1px solid #ccc;
  border-left: 2px solid #ed1c24;
  color: #000;
  font-size: 20px;
  height: 44px;
  line-height: 44px;
  margin-bottom: 10px;
  overflow: hidden;
  position: relative;
  text-align: center; }

.feed-author-article {
  background: #ffffff;
  border-bottom: 1px solid #cccccc;
  margin-bottom: 1px;
  padding: 15px 10px; }

.author-text {
  align-items: center;
  display: flex;
  height: 44px;
  width: 100%; }

.author-name {
  display: inline-block;
  width: 100%; }

.author-image {
  border: 1px solid #cccccc;
  border-radius: 50%;
  float: left;
  height: 40px;
  margin-right: 2.6%;
  margin-top: 2px;
  position: absolute;
  width: 40px; }

.article-title {
  -webkit-box-orient: vertical;
  display: -webkit-box;
  font-size: 14px;
  font-weight: normal;
  -webkit-line-clamp: 2;
  line-height: 20px;
  margin-bottom: 0;
  overflow: hidden;
  padding-right: 15px; }

.author-name {
  font-size: 16px;
  margin-bottom: 5px; }

.category-news .load-more-article .load-more-button {
  color: #000;
  display: inline-block;
  font-size: 14px;
  margin-top: 7px;
  padding-right: 15px;
  position: relative; }

.author-category-news .load-more-article .load-more-button {
  display: block;
  line-height: 35px;
  margin: 0;
  padding: 0;
  text-align: center; }
